import React from 'react'
import './contact.scss'
import Reveal from 'react-reveal/Reveal';

const Contact = () => (
    <div>
        <Reveal effect="growRight" >
        <div>
        <div className="contact-wrapper">
                <h1>Contact</h1>
            </div> 
        </div>
            
            
        </Reveal>
    </div>
)

export default Contact